	module.exports = {"data":{"post":{"html":"<p>O'Reilly Net: <a href=\"http://www.oreillynet.com/pub/a/sysadmin/2007/05/03/the-top-7-things-sysadmins-forget-to-do.html\">Top 7 Things System Administrators Forget to Do</a>. I'll admit to being guilty of all of these at some time or another, but I try especially hard to avoid number 7:</p>\n<blockquote><p>\n  Forgetting Courtesy</p>\n<p>  If you want support from management, consider remembering that the user you offend today could wind up on the board of directors. Regardless of that possibility, system administrators should always remember that their clients are internal and if you want to keep your job, be good to your clients.\n</p></blockquote>\n<p>We can be smart and help people without being arrogant. It's more fun that way.</p>","fields":{"title":"Top 7 Things System Administrators Forget to Do","date":"03 May 2007","url":"https://papascott.de/2007-05-03-top-7-things-system-administrators-forget-to-do/"}}},"pathContext":{"slug":"/2007-05-03-top-7-things-system-administrators-forget-to-do/","prev":{"fields":{"title":"Help","date":"02 May 2007","slug":"/2007-05-02-help/"}},"next":{"fields":{"title":"Corrupted UTF-8 characters with PHP and MySQL","date":"05 May 2007","slug":"/2007-05-05-corrupted-utf-8-characters-with-php-and-mysql/"}}}}

